  --medium-font: var(--red-hat-display-medium);
  --bold-font: var(--red-hat-display-bold);
  --primary-color: var(--sea);
  --secondary-color: var(--deep-lilac);
  --hover-main-color: var(--light-blue);
  --hover-secondary-color: var(--pale-violet);
  --btn-text-color: #fff;
  --heading-primary-color: var(--baltic-sea);
  --heading-secondary-color: var(--ghost-white);
  --paragraph-primary-color: var(--venus);
  --paragraph-secondary-color: var(--ghost-white);
  --lead-color: var(--sea);
  --footer-bg-color: var(--sea);
}

