* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.h1 {
  font-family: var(--main-font);
  color: var(--heading-primary-color);
  font-size: clamp(4rem, 3.2rem + 2.2535vi, 6.4rem);
  font-weight: 900;
  line-height: 1.1;
}

.h2 {
  font-family: var(--main-font);
  color: var(--heading-primary-color);
  font-size: 3.2rem;
  font-weight: 900;
  line-height: 1.12;
}

.h2--color-secondary {
  color: var(--heading-secondary-color);
}

.paragraph {
  font-family: var(--medium-font);
  color: var(--paragraph-primary-color);
  font-size: clamp(1.6rem, 1.53rem + .1878vi, 1.8rem);
  font-weight: 500;
  line-height: 1.62;
}

.paragraph--color-secondary {
  color: var(--paragraph-secondary-color);
}

.lead {
  font-family: var(--main-font);
  letter-spacing: .25em;
  text-transform: uppercase;
  color: var(--lead-color);
  font-size: 1.6rem;
  font-weight: 900;
  line-height: 1.62;
}

.button {
  cursor: pointer;
  font-family: var(--main-font);
  color: var(--btn-text-color);
  background-color: #0000;
  border: none;
  border-radius: 2.9rem;
  justify-content: center;
  align-items: center;
  gap: .4rem;
  padding: 1.6rem 0;
  font-size: 1.6rem;
  font-weight: 900;
  line-height: 1.62;
  display: flex;
}

.button--primary {
  background-color: var(--primary-color);
}

.button--secondary {
  background-color: var(--secondary-color);
}

.button--medium {
  inline-size: 19.3rem;
}

.button--short {
  inline-size: 13.9rem;
}

.button--primary > span {
  color: var(--hover-main-color);
}

.button--secondary > span {
  color: var(--hover-secondary-color);
}

@media (hover: hover) {
  .button--primary:hover {
    background-color: var(--hover-main-color);
  }

  .button--secondary:hover {
    background-color: var(--hover-secondary-color);
  }

  .button--primary:hover > span, .button--secondary:hover > span {
    color: #fff;
  }
}

@media (hover: none) {
  .button--primary:active {
    background-color: var(--hover-main-color);
  }

  .button--secondary:active {
    background-color: var(--hover-secondary-color);
  }

  .button--primary:active > span, .button--secondary:active > span {
    color: #fff;
  }
}

